body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.header-colour {
  background: #fc4a1a; /* fallback for old browsers */ /* Chrome 10-25, Safari 5.1-6 */
  background: -webkit-gradient(
    linear,
    left top, right top,
    from(#f7b733),
    to(#fc4a1a)
  );
  background: linear-gradient(
    to right,
    #f7b733,
    #fc4a1a
  ); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}

/* Landing Page */
.landing-body {
  background: #fc4a1a; /* fallback for old browsers */ /* Chrome 10-25, Safari 5.1-6 */
  background: -webkit-gradient(
    linear,
    left top, right top,
    from(#f7b733),
    to(#fc4a1a)
  );
  background: linear-gradient(
    to right,
    #f7b733,
    #fc4a1a
  ); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
  text-align: center;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.avatar-img {
  height: 250px;
  padding-top: 5em;
}

.banner-text {
  background-color: black;
  opacity: 0.8;
  width: 75%;
  margin: auto;
  border-radius: 10px;
}

.banner-text h1 {
  font-size: 50px;
  font-weight: bold;
  color: white;
}

.banner-text hr {
  border-top: 5px dotted white;
  width: 50%;
  margin: auto;
}

.banner-text p {
  color: white;
  font-size: 20px;
  padding: 1em;
}

.social-links {
  display: flex;
  justify-content: space-between;
  width: 50%;
  margin: auto;
}

.social-links i {
  color: white;
  font-size: 5em;
  padding: 8px;
}

/* About Me Page */
.aboutme-text {
  background-color: black;
  opacity: 0.8;
  width: 75%;
  margin: auto;
  margin-top: 10em;
  border-radius: 10px;
}

.aboutme-text h1 {
  font-size: 50px;
  font-weight: bold;
  color: white;
}

.aboutme-text p {
  color: white;
  font-size: 20px;
  padding: 1em;
}

.aboutme-text hr {
  border-top: 5px dotted white;
  width: 50%;
  margin: auto;
}

/* Resume Page */
.resume-body {
  background-color: black;
  opacity: 0.8;
  width: 75%;
  margin: auto;
  margin-top: 10em;
  border-radius: 10px;
  padding: 2em;
}

.resume-body h1 {
  color: white;
}

.resume-body hr {
  border-top: 5px dotted white;
  width: 50%;
  margin: auto;
}

.resume-body h2 {
  color: white;
  text-align: left;
}

.resume-information {
  color: white;
  text-align: left;
}

/* Projects Page */
.projects-body {
  background-color: white;
  width: 75%;
  margin: auto;
  margin-top: 10em;
  border-radius: 10px;
  padding: 2em;
}

.projects-body h1 {
  color: grey;
}

.projects-body hr {
  border-top: 5px dotted grey;
  width: 50%;
  margin: auto;
}

.projects-grid {
  display: flex;
}

/* Contacts Page */
.contact-grid {
  text-align: center;
  justify-content: center;
  width: 45%;
  height: 500px;
  background: black;
  opacity: 0.8;
  color: white;
  margin: auto;
  margin-top: 10em;
  margin-bottom: 5em;
  border-radius: 10px;
  padding: 20px;
}

.contact-grid h2 {
  color: white;
  font-family: "Anton", sans-serif;
}

.contact-grid hr {
  border-top: 5px dotted white;
  width: 50%;
  margin: auto;
}

.contact-list i {
  color: white;
  font-size: 66px;
  padding-right: 5rem;
}

